.wrapper {
  width: 100%;
  z-index: -1;
  position: absolute;
}


.visible {
  transition-timing-function: ease-in;
  transition: .5s;
  }

.notvisible {
  opacity: 0;
  transition-timing-function: ease-in;
  transition: .5s;

}

nav {
  right: 0;
  z-index: 1;
  position: fixed;
   display: grid;
   background-color: #0f0f0f;
   width: 100%;
}


* {
  font-family: 'Poppins', sans-serif;
  /* color: #F2EDD7FF; */
  color: white;

}

li {
  display: inline;
  position: relative;
}

a {
  overflow: hidden;
  color: white;
  text-decoration: none;
  font-size: 18px;
  font-weight: 530;
  animation: color-change 6s infinite;
  letter-spacing: 5px;
  margin-right: 20px;
  border-right: 90px;
  float: right;
  margin-top: 20px;
  padding: 10px;
  transition: border-color 0.66s ease-out;
  display: inline-block;
}

a:hover {
  /* opacity: 50%; */
  opacity: 1;
  cursor: pointer;
  text-shadow: 0 1 5px rgb(255, 252, 252);
  -webkit-text-stroke: 1px white;


  
}


/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
  /* IE and Edge */
  -ms-overflow-style: none;
  /* Firefox */
  scrollbar-width: none;
}


/* title */
.heading {
  top: 0;
  font-size: 200px;
  font-weight: 750;
  margin-top: 0px;
  padding-top: 200px;
  padding-left: 75px;
  padding-bottom: 500px;
  margin: 0px;
  opacity: 1;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke: 5px;
  position: relative;
}


